<template>
  <v-container class="bg-main">
    <v-row class="text-h5 mb-1 mt-3">
      <v-col cols="1"></v-col>
      {{ $store.getters.getMemberId }}님의
    </v-row>
    <v-row class="text-h5">
      <v-col cols="1"></v-col>
      <span class="mr-2">회원등급은</span> <span class="text-decoration-underline" @click="move('grade')">{{ $store.getters.getGname }}</span>
      <span>입니다.</span>
    </v-row>
    <v-row class="pt-5">
      <v-col cols="5" class="text-center">다음 날 예상 등급</v-col> <v-col class="text-center">{{ nextGrade }}</v-col></v-row
    >
    <v-row class="rounded-tr-xl white black-txt">
      <v-col class="pb-0 text-center font-weight-bold">쿠폰</v-col>
      <v-col class="pb-0 text-center font-weight-bold">포인트</v-col>
    </v-row>
    <v-row class="white black-txt">
      <v-col class="text-center" @click="move('coupon')"
        ><span class="main-txt text-decoration-underline mr-1">{{
          $store.getters.getCoupon.coupons == null ? 0 : $store.getters.getCoupon.coupons.length
        }}</span
        >장</v-col
      >
      <v-col class="text-center" @click="move('point')"
        ><span class="main-txt text-decoration-underline mr-1">{{ $store.getters.getMileagepoint }}</span
        >P</v-col
      >
    </v-row>

    <v-row class="white black-txt pt-5">
      <v-col class="font-weight-bold text-h5">최근 주문 상품 <span class="caption">(최근 1개월기준)</span></v-col>
    </v-row>

    <v-row class="bg-gray">
      <v-carousel
        :show-arrows="true"
        :show-arrows-on-hover="true"
        height="17%"
        :hide-delimiter-background="true"
        :hide-delimiters="true"
        class="pa-4"
        delimiter-icon="mdi-minus"
      >
        <v-carousel-item v-for="porder in orderProductList" :key="porder.ono">
          <v-row align="center" justify="center" class="pb-2">
            <v-col class="body-2">
              <div class="font-weight-bold mb-1">주문번호 {{ porder.ono }}</div>
              <div class="font-weight-bold">
                {{ porder.pbrand }}
              </div>
              <div class="font-weight-regular mb-2">
                {{ porder.pname }}
              </div>
              <div class="font-weight-bold mb-1">{{ String(porder.pprice).replace(/\B(?=(\d{3})+(?!\d))/g, ",") }}원</div>
              <div class="caption grey--text">
                <span class="pr-1">{{ porder.pcolor }}</span
                ><span>{{ porder.psize }}</span>
              </div>
              <div class="body-2 main-txt mt-2 font-weight-bold">{{ porder.ostate }}</div>
            </v-col>
            <v-col class="fill-height" cols="4">
              <v-img :src="porder.imgurl" width="100px"></v-img>
            </v-col>
          </v-row>
          <v-row justify="center" class="pa-0 ma-0">
            <v-btn @click="moveOrder(porder.ono)" plain block class="font-weight-bold">상세보기</v-btn>
          </v-row>
        </v-carousel-item>
      </v-carousel>
    </v-row>

    <v-row class="white black-txt">
      <v-col class="pb-0">
        <v-expansion-panels :flat="true">
          <v-expansion-panel class="pa-0">
            <v-expansion-panel-header class="body-1 font-weight-bold pa-0">회원정보</v-expansion-panel-header>
            <v-expansion-panel-content class="body-2"> <div @click="move('modify')">회원정보 수정</div> </v-expansion-panel-content>
            <v-expansion-panel-content class="body-2"><div @click="move('delete')">회원탈퇴</div></v-expansion-panel-content>
            <v-expansion-panel-content class="body-2"><div @click="move('myaddress')">배송지 관리</div></v-expansion-panel-content>
          </v-expansion-panel>
          <v-expansion-panel class="pa-0 mb-6">
            <v-expansion-panel-header class="body-1 font-weight-bold pa-0">쇼핑 정보</v-expansion-panel-header>
            <v-expansion-panel-content class="body-2"> <div @click="moveOrderPage('orderList')">주문 목록</div> </v-expansion-panel-content>
            <v-expansion-panel-content class="body-2"> <div @click="moveProduct('wishproductlist')">Wish List</div> </v-expansion-panel-content>
          </v-expansion-panel>
        </v-expansion-panels>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import OrderApi from "@/apis/order";
import GradeApi from "@/apis/grade";

export default {
  name: "Mypage",
  data: () => ({
    orderProductList: [
      {
        ono: 1,
        pbrand: "the CASHMERE",
        pname: "캐시미어 크롭 니트 가디건",
        pprice: 100000,
        imgurl: "http://newmedia.thehandsome.com/CM/2B/SS/CM2B0KCD230W_CP_W01.jpg/dims/resize/684x1032/",
        pcolor: "CP",
        psize: "85",
        ostate: "배송 준비중",
      },
      {
        ono: 2,
        pbrand: "the CASHMERE",
        pname: "캐시미어 블렌드 니트 가디건",
        pprice: 100000,
        imgurl: "http://newmedia.thehandsome.com/CM/2B/SS/CM2B0KCD231WP1_NL_W01.jpg/dims/resize/684x1032/",
        pcolor: "NL",
        psize: "85",
        ostate: "배송 준비중",
      },
    ],
    nextGrade: "",
    couponAmount: 0,
    active: false,
    idx: 1,
  }),
  methods: {
    move(where) {
      console.log(where);
      this.$router.push("/member/" + where);
    },
    moveOrder(ono) {
      console.log(ono);
    },
    moveProduct(where) {
      this.$router.push("/product/" + where);
    },
    moveOrderPage(where) {
      this.$router.push("/order/" + where);
    },
    getCouponList() {},
    async getNextGrade() {
      // 구매 금액 가져오기
      const promise = await OrderApi.getTotalOrderPrice(this.$store.getters.getMno);
      const result = await GradeApi.getGrade(promise.data);
      this.nextGrade = result.data;
    },

    async getOrderProductList() {
      // 주문 번호, 대표 상품 하나, 해당 상품의 url
      const promise = await OrderApi.getRecentOrder(this.$store.getters.getMno);
      console.log(promise);
    },
  },
  created() {
    if (this.$store.getters.getMemberId == "") {
      this.$router.push("/member/login");
    }
    this.getNextGrade();
  },
  mounted() {
    window.scrollTo(0, 0);
  },
};
</script>

<style scoped>
.bg-main {
  background-color: #c2935f;
  color: #ffff;
}
.black-txt {
  color: #0b0b0b;
}

.bg-gray {
  background-color: #f5f5f5;
}
</style>
